<template>
    <div class="wrapper">
        <textarea  class="i-input" ref="textarea" :style="textareaStyles" :rows="rows" :placeholder="placeholder" :value="currentValue" :max="max" @input="handleInput">
        </textarea>
    </div>
</template>

<script>
import calcTextareaHeight from './calcTextareaHeight.js'
export default {
    name: 'iTextarea',

    props: {
        placeholder: {
            type: String,
            default: ''
        },
        value: {
            type: [String, Number],
            default: ''
        },
        autosize: {
            type: [Boolean, Object],
            default: true
        },
        rows: {
            type: Number,
            default: 2
        },
        max: Number
    },

    data() {
        return {
            currentValue: this.value,
            textareaStyles: {}
        }
    },

    methods: {
        setCurrentValue(value) {
            if (value === this.currentValue) return;
            this.$nextTick(() => {
                this.resizeTextarea();
            });
            this.currentValue = value;
        },
        handleInput(event) {
            let value = event.target.value;
            if (this.number) value = Number.isNaN(Number(value)) ? value : Number(value);
            this.setCurrentValue(value);
        },
        resizeTextarea() {
            const autosize = this.autosize;
            if (!autosize) {
                return false;
            }

            const minRows = autosize.minRows;
            const maxRows = autosize.maxRows;

            this.textareaStyles = calcTextareaHeight(this.$refs.textarea, minRows, maxRows);
        }
    },

    watch: {
        value(val) {
            this.setCurrentValue(val);
        }
    },

    mounted() {
        this.resizeTextarea();
    }
}    
</script>

<style>
.wrapper {
    display: inline-block;
    width: 100%;
    position: relative;
    vertical-align: middle;
    margin: 0;
    padding: 0;
}

textarea {
    max-width: 100%;
    vertical-align: bottom;
    font-size: 14px;
}

.i-input {
    display: inline-block;
    width: 300px;
    line-height: 1.5;
    padding: 4px 7px;
    border: 1px solid #d7dde4;
    border-radius: 4px;
    color: #657180;
    background-color: #fff;
    background-image: none;
    position: relative;
    cursor: text;
}

.i-input:hover {
    border-color: #42b983;
}

.i-input:focus {
    border-color: #42b983;
    outline: 0;
}

</style>


